.sidebar {
  display: block;
  position: absolute;
  z-index: 3;
  top: 75px;
  left: -250px;
  width: 250px;
  height: calc(100vh - 75px);
  background-color: #fff;
  border-right: 1px solid #F2F2F2;
  padding: 30px 20px 30px 0;
  overflow-y: auto;
  @include tr(0.25s);

  @include r(767) {
    padding: 20px 20px 20px 0; }

  &.is-show {
    left: 0;
    & + .page-main {
      width: calc(100% - 250px);
      padding: 45px 45px;
      left: 250px;
      @include tr(0.25s);
      @include r(959) {
        width: 100%;
        padding: 30px;
        left: 0;
        overflow: hidden;
        &::before {
          opacity: 1; } }
      @include r(767) {
        padding: 20px 15px 30px; } } }

  & + .page-main {
    width: 100%;
    padding: 45px 30px;
    left: 0;
    @include tr(0.25s);
    @include r(959) {
      padding: 30px 30px 45px; } } }

.sidebar-box {
  display: block;
  position: relative;

  .uk-nav {

    li {
      a {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        position: relative;
        padding: 12px 12px 12px 30px;
        border-radius: 0px 20px 20px 0px;

        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 19px;
        color: #646464;
        @include tr(0.25s);
        @include hh {
          background: rgba(244, 97, 25, 0.05);
          color: $accent;
          @include ico() {
            color: $accent; } }

        @include ico() {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 24px;
          height: 24px;
          flex: 0 0 24px;
          font-size: 24px;
          color: #646464;
          margin-right: 30px;
          @include tr(0.25s); }

        &::before {
          content: '';
          display: block;
          position: absolute;
          top: 0;
          left: 0;
          width: 5px;
          height: 100%;
          background: #F46119;
          border-radius: 0px 5px 5px 0px;
          opacity: 0;
          @include tr(0.25s); }

        span.count {
          display: flex;
          justify-content: center;
          align-items: center;
          background: $accent;
          width: 18px;
          height: 18px;
          font-style: normal;
          font-weight: 400;
          font-size: 10px;
          line-height: 13px;
          color: #FFFFFF;
          border-radius: 50%;
          margin-left: auto; } } }


    li.uk-nav-header {
      padding-left: 30px;
      font-style: normal;
      font-weight: bold;
      font-size: 12px;
      line-height: 16px;
      color: #767676;
      text-transform: uppercase;
      margin-top: 10px; }

    li.uk-active {
      a {
        background: rgba(244, 97, 25, 0.05);
        color: $accent;
        @include ico() {
          color: $accent; }
        &::before {
          opacity: 1; } } } } }
