.widjet.--filters {
  margin-bottom: 24px;

  .search__input {
    max-width: 100%; }

  .nice-select {
    width: 100%;
    flex-grow: 1; } }

.favourites-game {
  .game-card__media {
    max-height: 130px!important;
    min-height: 130px!important;
    @include r(575) {
      min-height: auto!important;
      max-height: none!important; } } }

.widjet.--my-list {
  .widjet__body {
    background: none;
    padding: 0; } }

.my-game-list {
  list-style-type: none;
  padding: 0;
  margin: 0; }

.my-game-item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 10px 30px 10px 10px;
  background: #fff;
  border-radius: 10px;
  & + .my-game-item {
    margin-top: 12px; }
  &__img {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 85px;
    height: 55px;
    flex: 0 0 85px;
    border-radius: 10px;
    overflow: hidden; }
  &__letter {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 85px;
    height: 55px;
    flex: 0 0 85px;
    border-radius: 10px;

    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    line-height: 40px;
    color: #FFFFFF; }

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
    color: #353535;
    flex-grow: 1;
    margin: 0 20px;
    @include r(767) {
      margin: 0 10px; } }

  &__more {
    a {
      font-size: 6px;
      color: #646464;
      @include tr(0.25s);
      @include h {
        color: $accent; } } } }

.creat-list-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  width: 100%;
  height: 40px;
  border: none;
  background: rgba(244, 97, 25, 0.05);
  border-radius: 10px;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.1;
  color: #F65100;
  @include tr(0.25s);
  @include ico() {
    margin-right: 10px;
    font-size: 20px; }
  @include h {
    background: $accent;
    color: #fff; } }
