
.widjet {
  display: block;
  position: relative;
  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 12px;

    h3.uk-text-lead {
      margin-bottom: 0; }

    a {
      font-style: normal;
      font-weight: 700;
      font-size: 16px;
      line-height: 21px;
      color: #F65100; } }

  &__body {
    background: #fff;
    border-radius: 10px;
    padding: 20px 30px;
    @include r(767) {
      padding: 15px; }

    & + .widjet__body {
      margin-top: 15px; } }

  & + .widjet {
    margin-top: 24px; } }

.widjet.--profile {
  .widjet__body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include r(767) {
      flex-direction: column;
      align-items: flex-start;

      .uk-button {
        margin-top: 20px; } } } }

.user-info {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  &__avatar {
    width: 110px;
    height: 110px;
    flex: 0 0 110px;
    overflow: hidden;
    border-radius: 50%;
    margin-right: 20px; }
  &__box {
    display: block;
    position: relative; }
  &__title {
    font-style: normal;
    font-weight: normal;
    font-size: 26px;
    line-height: 35px;
    color: #353535;
    @include fz(26, 20); }
  &__text {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    color: #767676;
    margin-top: 6px; } }

.widjet.--activity {
  .widjet__body {
    padding: 15px; } }

.widjet-game {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  @include r(639) {
    flex-direction: column;
    align-items: stretch; }
  &__media {
    border-radius: 10px;
    overflow: hidden;
    margin-right: 20px;
    @include r(639) {
      margin-right: 0;
      margin-bottom: 10px; }
    img {
      width: 100%; } }
  &__info {
    display: block;

    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    color: #767676;
    @include fz(14, 12);
    b {
      display: inline-block;
      margin-left: 5px;
      font-style: normal;
      font-weight: 700;
      font-size: 14px;
      line-height: 19px;
      color: #616161;
      @include fz(14, 12); } }
  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    color: #353535;
    @include fz(16, 14); }
  &__record {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    color: #767676;
    margin: 6px 0; }
  &__last-played {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    color: #767676; }
  &__starting {
    display: flex;
    align-items: center;

    [data-uk-icon="arrow-up"] {
      width: 16px;
      height: 16px;
      border-radius: 3px;
      background: #0DA800;
      color: #fff;
      margin-top: -2px;
      margin-left: 10px; }

    [data-uk-icon="arrow-down"] {
      width: 16px;
      height: 16px;
      border-radius: 3px;
      background: #C60000;
      color: #fff;
      margin-top: -2px;
      margin-left: 10px; } }

  &__quantity {
    display: flex;
    align-items: center; } }

.widjet-game + .widjet-game-info {
  margin-top: 15px; }

.widjet-game-info {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  background: #F5F5F5;
  border-radius: 10px;
  padding: 15px 30px;
  @include r(1199) {
    padding: 15px; }
  @include r(767) {
    padding: 15px 10px; }
  & > * {
    margin: 5px; }

  &__title {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    color: #5F5F5F; }
  &__progress {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    color: #707070;
    .progress-box {
      display: block;
      position: relative;
      width: 200px;
      height: 8px;
      background: #DFDFDF;
      border-radius: 5px;
      margin-left: 10px;
      @include r(767) {
        width: 150px; } }
    .progress-line {
      position: absolute;
      top: 0;
      left: 0;
      height: 8px;
      background: #F65100;
      border-radius: 5px; } }
  &__acheivement {
    ul {
      list-style-type: none;
      padding: 0;
      margin: 0;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      li {
        width: 35px;
        height: 35px;
        flex: 0 0 35px;
        & + li {
          margin-left: 3px; }
        &:last-child {
          display: flex;
          justify-content: center;
          align-items: center;
          background: #DFDFDF;
          border-radius: 3px;
          font-style: normal;
          font-weight: 700;
          font-size: 12px;
          line-height: 1;
          color: #606060; } } } } }

.widjet.--upload {
  .widjet__body {
    display: flex;
    align-items: center;
    & > .uk-button {
      margin-left: 10px;
      @include r(639) {
        margin-left: 0;
        margin-top: 10px; } }
    @include r(639) {
      flex-direction: column;
      align-items: stretch; } } }

.widjet.--badges {
  .widjet__body {
    padding: 30px;
    @include r(767) {
      padding: 15px 15px 5px; } } }

.badges-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  li {
    margin: 0 10px 10px 0;
    &:last-child {
      margin-right: 0; } } }

.widjet.--games {
  .widjet__body {
    padding: 45px 30px;
    @include r(767) {
      padding: 15px 15px 5px; } } }

.games-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  li {
    margin: 0 10px 10px 0;
    &:last-child {
      margin-right: 0; } } }
