body.dark-theme {

  .nice-select {
    background: #1A2634;
    .current {
      color: #fff; }
    .list {
      background: #1A2634; }
    .option.selected.focus,
    .option:hover {
      background: #323446; } }

  .uk-button-secondary {
    background: #323446; }

  .uk-button-primary {
    background: #1A2634;
    color: #fff; }

  .uk-text-lead {
    color: #fff; }

  .ico_more {
    color: #fff;
    @include h {
      color: $accent; } }

  .uk-subnav-pill {
    > .uk-active {
      > a {
        background: #fff;
        color: #000; } }
    > * {
      > :first-child {
        background: #0C1826;
        color: #fff; } } }

  .uk-modal-dialog {
    background: #0C1826; }

  .uk-textarea {
    background: #1A2634; }

  .uk-form-custom {
    .uk-button-default {
      background: #1A2634;
      color: #ACACAC; } }

  .uk-button-danger {
    background: #323446; }

  .uk-modal-close-default {
    background: #323446;
    @include h {
      background: $accent;
      color: #fff; } }

  .swiper-button-prev::before,
  .swiper-button-next::before {
    color: #0C1826; }

  .swiper-button-prev::after,
  .swiper-button-next::after {
    background: #fff; }

  .form-login {
    color: #ACACAC;
    background: #0C1826;
    box-shadow: 0px 30px 70px rgba(0, 0, 0, 0.03);

    .uk-heading-line>:after,
    .uk-heading-line>:before {
      border-bottom: 1px solid #1A2634; }

    .uk-hr, hr {
      border-top: 1px solid #1A2634; }

    .uk-input {
      background: #1A2634;
      border: 1px solid #1A2634; } }


  .page-wrapper {
    background: #040615; }

  .page-header {
    &__inner {
      background: #0C1826; }
    &__sidebar {
      border-right: 1px solid #030410; }
    &__menu-btn .menu-btn {
      color: #fff; } }

  .search {
    &__input {
      background: #1A2634; }
    &__btn button {
      color: #fff; } }

  .action-btn {
    color: #fff; }

  .sidebar {
    background: #0C1826;
    border-right: 1px solid #030410; }

  .sidebar-box {
    .uk-nav {
      li {
        a {
          color: #fff;
          [class*='ico_'] {
            color: #DADADA; } } }

      li.uk-active {
        a {
          color: $accent;
          [class*='ico_'] {
            color: $accent; } } } } }


  .recommend-slide {
    background: #0C1826;
    box-shadow: 0px 30px 70px rgba(0, 0, 0, 0.03); }

  .game-card {
    &__box {
      background: #0C1826;
      box-shadow: 0px 30px 70px rgba(0, 0, 0, 0.03); }
    &__title,
    &__genre,
    &__rating,
    &__price,
    &__platform {
      color: #fff; } }

  .widjet {
    &__body {
      background: #0C1826;
      box-shadow: 0px 30px 70px rgba(0, 0, 0, 0.03); }

    .widjet-game {
      &__title {
        color: #fff; }
      &__record,
      &__last-played {
        color: #A1A1A1; } }

    .widjet-game-info {
      background: #1A2634;

      &__title {
        color: #fff; }

      &__progress {
        .progress-box {
          background: #0C1826; } }

      &__acheivement {
        ul {
          li:last-child {
            background: #0C1826;
            color: #fff; } } } } }

  .user-info {
    &__title {
      color: #fff; }
    &__text {
      color: #A1A1A1; } }

  .my-game-item {
    background: #0C1826;
    &__title,
    &__more a {
      color: #fff; } }

  .chat-user-list,
  .chat-messages-box {
    background: #0C1826; }

  .user-item {
    background: #0C1826;
    &__name {
      color: #fff; }
    &__text {
      color: #A1A1A1; } }

  .messages-item.--your-message {
    .messages-item__text {
      background: #1A2634;
      color: #fff; } }

  .chat-messages-form {
    .chat-messages-form-controls {
      background: #1A2634;
      .chat-messages-input {
        color: #fff; } }
    .chat-messages-form-btn,
    .chat-messages-form-btns {
      button {
        color: #fff; } } }

  .friend-requests-item {
    &__name {
      color: #fff; } }

  .friend-requests-item__action {
    button.reject {
      background: #1A2634;
      color: #fff; } }

  .activities-item {
    background: #1A2634;
    &__title {
      color: #fff; }
    &__date {
      color: #A1A1A1; } }

  .gallery,
  .game-profile-card,
  .game-profile-price {
    background: #0C1826;

    &__intro {}
    color: #A1A1A1;
    &__list {
      list-style-type: none;
      padding: 0;
      margin: 0;
      li {
        & > *:first-child {
          color: #A1A1A1; } } }
    &__type {
      color: #A1A1A1;

      li span {
        background: #1A2634;
        color: #ACACAC; } }

    &__value {
      color: #fff; } }


  .stream-item {
    background: #0C1826;
    &__title {
      color: #fff; }
    &__nicname,
    &__time {
      color: #ACACAC; } }



  #modal-help {
    h4 {
      color: #fff; }

    ul {
      li {
        color: #fff; } } } }
