.page-header {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 3;
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 75px;
    background-color: #fff; }
  &__sidebar {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 250px;
    height: 75px;
    border-right: 1px solid #F2F2F2;
    padding: 0 30px;
    @include r(767) {
      width: auto;
      padding: 0 10px 0 15px;
      border-right: 0; } }
  &__menu-btn {
    margin-right: 30px;
    margin-bottom: -10px;
    @include r(767) {
      margin-right: 10px; }
    .menu-btn {
      font-size: 20px;
      color: #646464;
      border: none;
      outline: none;
      @include tr(0.25s);
      @include hh {
        color: $accent; } } }
  &__logo {
    display: block;
    position: relative; }
  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-grow: 1;
    padding: 0 45px;
    height: 75px;
    box-shadow: 11px 7px 10px 0px #0000000f;
    @include r(767) {
      padding: 0 15px 0 10px; } }
  &__search {
    display: block;
    position: relative;
    flex-grow: 1;
    @include r(767) {
      display: none; }
    .search {
      @include r(1199) {
        max-width: 300px; } } }

  &__action {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    @include r(767) {
      flex-grow: 1; }
    & > * {
      margin-right: 24px;
      @include r(767) {
        margin-right: 15px; }
      &:last-child {
        margin-right: 0; } } } }


.action-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 24px;
  height: 24px;
  flex: 0 0 24px;
  font-size: 24px;
  color: #646464;
  @include tr(0.25s);
  @include h {
    color: $accent; }

  span {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 8px;
    height: 8px;
    background: #F46119;
    border: 1px solid #FFFFFF;
    border-radius: 50%;
    box-sizing: border-box; } }

.profile {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  flex: 0 0 32px; }
