.price-range {
  display: flex;
  align-items: center;
  label {
    margin-right: 10px;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
    color: #5F5F5F; }

  .uk-range::-webkit-slider-runnable-track {
    background: rgba(244, 97, 25, 0.1);
    border-radius: 5px; }

  .uk-range::-webkit-slider-thumb {
    -webkit-appearance: none;
    margin-top: -7px;
    height: 15px;
    width: 15px;
    border-radius: 500px;
    background: $accent;
    border: 1px solid $accent; } }

.gallery {
  background: #fff;
  padding: 15px;
  border-radius: 10px;
  position: relative;

  .swiper-pagination-bullets.swiper-pagination-horizontal {
    bottom: -30px; } }

.gallery-small {
  margin-top: 15px;
  @include r(639) {
    display: none; }
  .swiper {
    padding: 0;
    margin: 0; } }

.game-profile-card {
  display: block;
  position: relative;
  padding: 15px 15px 7px 15px;
  background: #fff;
  border-radius: 10px;
  margin-bottom: 12px;
  &__media {
    display: block;
    position: relative;
    img {
      width: 100%; } }
  &__intro {
    margin: 15px 0;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 21px;
    color: #767676; }
  &__list {
    list-style-type: none;
    padding: 0;
    margin: 0;
    li {
      display: flex;
      align-items: center;
      & + li {
        margin-top: 5px; }
      & > *:first-child {
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 21px;
        color: #767676;
        margin-right: 10px; } } }

  &__type {
    list-style-type: none;
    padding: 0;
    margin: 15px 0 0 0;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    li {
      margin: 0 8px 8px 0;
      &:last-child {
        margin-right: 0; }
      span {
        display: flex;
        justify-content: center;
        align-items: center;
        background: #F5F5F5;
        border-radius: 5px;
        padding: 0 5px;
        height: 25px;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 19px;
        color: #5F5F5F; } } } }

.game-profile-price {
  display: block;
  position: relative;
  padding: 15px;
  background: #fff;
  border-radius: 10px;
  margin-bottom: 12px;
  &__value {
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    color: #353535;
    margin: 30px 0;
    text-align: center; }

  .uk-button {
    height: 50px;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    @include ico() {
      margin-right: 10px; }
    & + .uk-button {
      margin-top: 8px; } } }
