#toggle {
  -webkit-appearance: none;
  appearance: none;
  height: 40px;
  width: 75px;
  background-color: #15181f;
  position: absolute;
  left: 20px;
  bottom: 20px;
  z-index: 5;
  border-radius: 20px;
  outline: none;
  cursor: pointer;
  box-shadow: 2px 2px 7px 0px #00000024;
  &:after {
    content: "";
    position: absolute;
    height: 30px;
    width: 30px;
    background-color: #ffffff;
    top: 5px;
    left: 7px;
    border-radius: 50%; } }

.dark-theme {
  background-color: #15181f;
  color: #e5e5e5; }

.dark-theme #toggle {
  background-color: #ffffff; }

.dark-theme #toggle:after {
  background-color: transparent;
  box-shadow: 10px 10px #15181f;
  top: -4px;
  left: 30px; }


body.no-scroll {
  overflow: hidden; }

.page-wrapper {
  min-width: 320px;
  min-height: 100vh;
  max-width: 1920px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #F5F5F5;
  position: relative;
  .page-content {
    display: flex;
    align-items: stretch;
    padding-top: 75px;
    flex-grow: 1; }
  .page-main {
    display: block;
    position: relative;
    padding: 45px;
    height: calc( 100vh - 75px);
    overflow-x: hidden;
    overflow-y: auto;

    scrollbar-color: $accent #E2EBEC;
    scrollbar-width: thin!important;
    -moz-appearance: none !important;

    &::-webkit-scrollbar {
      width: 6px; }

    &::-webkit-scrollbar-track {
      background-color: #E2EBEC; }

    &::-webkit-scrollbar-thumb {
      background-color: $accent; }

    @include r(767) {
      padding: 20px 15px 40px; }

    &::before {
      content: '';
      display: block;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(#000, 0.5);
      opacity: 0;
      pointer-events: none;
      z-index: 2; } } }


.scroll-box {
  scrollbar-color: $accent #E2EBEC;
  scrollbar-width: thin!important;
  -moz-appearance: none !important;

  &::-webkit-scrollbar {
    width: 4px; }

  &::-webkit-scrollbar-track {
    background-color: #E2EBEC; }

  &::-webkit-scrollbar-thumb {
    background-color: $accent; } }

a {
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 21px;
  color: #F46119;

  @include tr(0.25s);
  @include h {
    color: $accent; } }

.search {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  &__input {
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
    max-width: 550px;
    height: 45px;
    background: #F5F5F5;
    border-radius: 25px;
    padding: 0 20px;
    @include ico() {
      font-size: 16px;
      color: #6E6E6E;
      margin-right: 10px; }
    input {
      width: 100%;
      background: none;
      border: none;

      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 20px;
      color: #6E6E6E;

      @include placeholder() {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 20px;
        color: #6E6E6E; }

      @include hh {
        outline: none;
        border: none; } } }
  &__btn {
    margin-left: 12px;
    button {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 24px;
      height: 24px;
      flex: 0 0 24px;
      font-size: 24px;
      color: #646464;
      outline: none;
      border: none;
      cursor: pointer;
      @include tr(0.25s);
      @include hh {
        color: $accent; } } } }

.social {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
  list-style-type: none;
  li {
    margin: 0 20px 0 0;
    &:last-child {
      margin: 0; } } }

.uk-input {
  height: 60px;
  line-height: 60px;
  background: #F2F2F2;
  border: 1px solid #F5F5F5;
  box-sizing: border-box;
  border-radius: 15px;
  padding: 0 20px;

  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  color: #000;

  @include placeholder() {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    color: #6E6E6E; }

  @include hh {
    background: #F2F2F2;
    border: 1px solid #F5F5F5; } }

.uk-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  border-radius: 15px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  text-transform: none;
  @include tr(0.25s); }

.uk-button-secondary {
  background-color: #8F8F8F;
  @include hh {
    background-color: darken(#8F8F8F, 10%); } }

.uk-button-primary {
  background: #F5F5F5;
  color: #5F5F5F;
  @include hh {
    background-color: darken(#F5F5F5, 20%);
    color: #fff; } }

.uk-button-danger {
  background: $accent;
  color: #fff;
  @include hh {
    background: darken($accent, 10%); } }

.uk-button-link {
  color: $accent; }

.ico_more {
  width: 24px;
  height: 24px;
  flex: 0 0 24px;
  font-size: 6px;
  border: none;
  background: none;
  color: #353535;
  @include tr(0.25s);
  @include h {
    color: $accent; } }

.uk-form-label {
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 24px;
  color: #6F6F6F; }

.uk-input {
  background: #F5F5F5;
  border-radius: 15px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 19px;
  color: #6E6E6E;
  border: none;
  resize: none;
  @include hh {
    background: #F5F5F5; } }

.uk-textarea {
  height: 150px;
  background: #F5F5F5;
  border-radius: 15px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 19px;
  color: #6E6E6E;
  border: none;
  resize: none;
  padding: 10px 15px;
  @include hh {
    background: #F5F5F5; } }

.uk-form-custom {
  .uk-button-default {
    border: none;
    background: #F5F5F5;
    border-radius: 15px;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    color: #5F5F5F;
    .uk-icon,
    [class*="ico_"] {
      margin-right: 10px; } } }

.nice-select {
  height: 40px;
  line-height: 40px;
  background: #F5F5F5;
  border-radius: 10px;
  border: none;

  .current {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 21px;
    color: #6F6F6F; } }

.swiper {
  margin: -20px;
  padding: 20px; }

.swiper-button-prev,
.swiper-rtl .swiper-button-next {
  width: 40px;
  height: 40px;
  flex: 0 0 40px;
  &:after {
    display: none; }
  @include icon(arrow-circle-left);
  &::before {
    width: 40px;
    height: 40px;
    flex: 0 0 40px;
    font-size: 40px;
    color: #fff;
    @include tr(0.25s); }
  @include h {
    &::before {
      color: $accent;
      @include tr(0.25s); } } }

.swiper-button-next,
.swiper-rtl .swiper-button-prev {
  width: 40px;
  height: 40px;
  flex: 0 0 40px;
  &:after {
    display: none; }
  @include icon(arrow-circle-right);
  &::before {
    width: 40px;
    height: 40px;
    flex: 0 0 40px;
    font-size: 40px;
    color: #fff;
    @include tr(0.25s); }
  @include h {
    &::before {
      color: $accent;
      @include tr(0.25s); } } }

.swipper-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 40px;
  bottom: 40px;
  @include r(575) {
    display: none; }
  .swiper-button-prev {
    position: relative;
    left: auto;
    top: auto; }
  .swiper-button-next {
    position: relative;
    right: auto;
    top: auto;
    margin-left: 10px; } }

.swiper-button-prev,
.swiper-button-next {
  box-shadow: 0 5px 15px rgba(#000, 0.08);
  border-radius: 50%;
  @include r(575) {
    display: none; }
  &::after {
    content: '';
    display: block;
    width: 35px;
    height: 35px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #000;
    border-radius: 50%;
    z-index: -1; } }

.swiper-horizontal>.swiper-pagination-bullets {
  bottom: -5px; }

.swiper-pagination-bullet {
  background: #6d6d6dcc;
  border-radius: 5px;
  @include tr(0.25s); }

.swiper-pagination-bullet-active {
  background-color: $accent;
  width: 40px;
  border-radius: 5px; }

.uk-subnav {
  margin-left: -10px; }

.uk-subnav>* {
  padding-left: 10px;
  padding-bottom: 10px; }

.uk-subnav>*>:first-child {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 5px 15px;
  background-color: #fff;
  box-shadow: 0px 30px 70px rgba(0, 0, 0, 0.1);
  border-radius: 12px;

  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 19px;
  color: #000000;
  text-transform: none;
  @include h {
    background: #fff;
    color: $accent; } }

.uk-subnav-pill>.uk-active>a {
  background: #000;
  color: #fff; }

.uk-lightbox {
  background: #000000f5; }
