.widjet.--wallet {
  .widjet__body {
    display: flex;
    align-items: center;
    height: 212px;
    @include r(639) {
      height: auto;
      justify-content: center; } } }

.wallet-info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  @include r(639) {
    align-items: center; } }
.wallet-value {
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 48px;
  color: #353535; }
.wallet-label {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 21px;
  color: #616161; }

.widjet.--payment-method {
  .widjet__body {
    padding: 20px 16px; } }

.payment-card {
  background: linear-gradient(257.17deg, #00A911 0%, #009456 100%);
  box-shadow: 0px 0px 50px rgba(65, 220, 13, 0.1);
  border-radius: 10px;
  padding: 20px 30px;
  margin: 0 auto;
  max-width: 320px;
  height: 170px;
  box-sizing: border-box;
  @include r(639) {
    padding: 20px;
    height: 150px; }
  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  &__chip {}
  &__logo {}
  &__number {
    margin: 20px 0;
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 29px;
    color: #FFFFFF;
    text-align: center;
    @include fz(22, 20);
    @include r(639) {
      margin: 15px 0; } }
  &__value {
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 27px;
    color: #FFFFFF;
    @include fz(20, 18); } }

.widjet.--activities {
  .widjet__body {
    padding: 0;
    background: none; } }


.activities-list {
  list-style-type: none;
  padding: 0;
  margin: 0; }

.activities-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  background-color: #fff;
  border-radius: 10px;
  @include r(639) {
    padding: 12px; }
  & + .activities-item {
    margin-top: 12px; }
  &__logo {
    width: 65px;
    height: 65px;
    flex: 0 0 65px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 10px;
    @include r(639) {
      width: 40px;
      height: 40px;
      flex: 0 0 40px; } }
  &__info {
    flex-grow: 1; }
  &__title {
    display: block;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    color: #353535;
    @include line(1);
    @include fz(16, 12); }
  &__date {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    color: #767676;
    @include fz(14, 12); }
  &__price {
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
    color: #E60000;
    white-space: nowrap;
    @include fz(14, 12); } }
