.friend-requests-list {
  list-style-type: none;
  padding: 0;
  margin: 0; }
.friend-requests-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  & + .friend-requests-item {
    margin-top: 24px; }
  &__avatar {
    width: 50px;
    height: 50px;
    flex: 0 0 50px; }
  &__name {
    flex-grow: 1;
    padding: 0 12px;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    color: #353535; }
  &__action {
    display: flex;
    align-items: center;
    button {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      border: none;
      width: 40px;
      height: 40px;
      flex: 0 0 40px;
      border-radius: 15px;
      & + button {
        margin-left: 12px; }
      @include ico() {
        font-size: 16px;
        width: 16px;
        height: 16px; } }
    button.confirm {
      background: #F46119;
      color: #fff; }
    button.reject {
      background: #F5F5F5;
      color: #5F5F5F; } } }
