.stream-item {
  display: block;
  position: relative;
  background: #fff;
  border-radius: 10px;
  padding: 10px;
  &__box {
    display: block;
    position: relative; }
  &__media {
    display: block;
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    a {
      display: block;
      position: relative;
      @include icon(play-circle);
      &::before {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        pointer-events: none;
        color: #fff;
        font-size: 40px;
        opacity: 0;
        @include tr(0.3s); }
      @include hh {
        &::before {
          opacity: 1; } }

      img {
        width: 100%; } } }

  &__info {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 10px;
    left: 10px;
    z-index: 2; }
  &__status {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 22px;
    background: rgba(0, 0, 0, 0.6);
    border-radius: 8px;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    color: #FFFFFF;
    &::before {
      content: '';
      display: inline-block;
      width: 8px;
      height: 8px;
      background: #F00000;
      border-radius: 50%;
      margin-right: 6px; } }
  &__count {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 22px;
    background: rgba(0, 0, 0, 0.6);
    border-radius: 8px;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    color: #FFFFFF;
    margin-top: 5px;
    @include icon(eye);
    &::before {
      margin-right: 6px; } }
  &__body {
    padding: 10px; }
  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    color: #353535;
    @include line(1); }
  &__nicname {
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
    color: #767676; }
  &__time {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    color: #767676; } }
