.uk-modal-dialog {
  width: 450px;
  padding: 20px;
  border-radius: 25px;
  background: #FFFFFF; }

.uk-modal-title {
  text-align: center;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 32px;
  color: #6F6F6F; }

.uk-modal-close-default {
  background: #D3D3D3;
  border-radius: 50%;
  color: #000; }

#modal-help {
  h4 {
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    color: #6F6F6F; }
  ul {
    list-style-type: none;
    padding: 0;
    motion: 0;
    li {
      display: flex;
      align-content: center;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 19px;
      color: #353535;

      & + li {
        margin-top: 20px; }

      img {
        margin-right: 10px; }

      a {
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 19px;
        color: #F46119; } } } }
