@font-face {
    font-family: "icons";
    src: url('../fonts/icons.eot');
    src: url('../fonts/icons.eot?#iefix') format('eot'), url('../fonts/icons.woff') format('woff'),url('../fonts/icons.woff2') format('woff2'), url('../fonts/icons.ttf') format('truetype'), url('../fonts/icons.svg#icons') format('svg'); }


@mixin icon-styles {
    font-family: "icons";
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-style: normal;
    font-variant: normal;
    font-weight: normal;
    // speak: none; // only necessary if not using the private unicode range (firstGlyph option)
    text-decoration: none;
    text-transform: none;

    display: inline-block;
    text-decoration: inherit;
    // width: 1em
    line-height: 1em; }


%icon {
    @include icon-styles; }

@function icon-char($filename) {
    $char: "";

    @if $filename == add-square {
        $char: "\E001"; }

    @if $filename == apple {
        $char: "\E002"; }

    @if $filename == arrow-circle-left {
        $char: "\E003"; }

    @if $filename == arrow-circle-right {
        $char: "\E004"; }

    @if $filename == attach-circle {
