    @if $filename == clipboard-text {
        $char: "\E008"; }

    @if $filename == close-circle {
        $char: "\E009"; }

    @if $filename == community {
        $char: "\E00A"; }

    @if $filename == edit {
        $char: "\E00B"; }

    @if $filename == emoji-happy {
        $char: "\E00C"; }

    @if $filename == eye {
