    @if $filename == gallery {
        $char: "\E010"; }

    @if $filename == help {
        $char: "\E011"; }

    @if $filename == home {
        $char: "\E012"; }

    @if $filename == info-circle {
        $char: "\E013"; }

    @if $filename == market {
        $char: "\E014"; }

    @if $filename == menu {
        $char: "\E015"; }

    @if $filename == message {
        $char: "\E016"; }

    @if $filename == microphone {
        $char: "\E017"; }

    @if $filename == more {
        $char: "\E018"; }

    @if $filename == notification {
        $char: "\E019"; }

    @if $filename == play-circle {
        $char: "\E01A"; }

    @if $filename == profile {
        $char: "\E01B"; }

    @if $filename == report {
        $char: "\E01C"; }

    @if $filename == search {
        $char: "\E01D"; }

    @if $filename == shopping-cart {
        $char: "\E01E"; }

    @if $filename == star {
        $char: "\E01F"; }

    @if $filename == store {
        $char: "\E020"; }

    @if $filename == streams {
        $char: "\E021"; }

    @if $filename == tick-circle {
        $char: "\E022"; }

    @if $filename == trash {
        $char: "\E023"; }

    @if $filename == video-circle {
        $char: "\E024"; }

    @if $filename == wallet {
        $char: "\E025"; }

    @if $filename == windows {
        $char: "\E026"; }

    @return $char; }


@mixin icon($filename, $insert: before, $extend: true) {
    &:#{$insert} {
        @if $extend {
            @extend %icon; }
        @else {
            @include icon-styles; }

        content: icon-char($filename); } }

