.chat-user-list {
  display: block;
  position: relative;
  padding: 20px;
  background: #fff;
  border-radius: 10px;
  height: 100%;
  @include r(767) {
    padding: 10px; }
  &__box {
    display: block;
    position: relative; }
  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
    @include r(767) {
      margin-bottom: 15px; }

    .avatar {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 40px;
      flex: 0 0 40px; }

    a.ico_edit {
      margin-left: auto;
      margin-right: 30px;
      color: #646464;
      width: 20px;
      height: 20px;
      font-size: 20px; }

    a.ico_more {
      width: 20px;
      height: 20px;
      font-size: 6px;
      color: #646464;
      margin-right: 10px; } }

  &__search {
    margin-bottom: 30px;
    @include r(767) {
      margin-bottom: 15px; }
    .search__input {
      max-width: 100%; } }
  &__body {
    margin: 0 -15px;
    @include r(767) {
      margin: 0 0 0 -10px; }
    ul {
      list-style-type: none;
      padding: 0;
      margin: 0; } } }

.user-item {
  padding: 15px 20px;
  background: #FFFFFF;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  @include r(767) {
    padding: 5px 10px; }
  &__avatar {
    display: block;
    width: 50px;
    height: 50px;
    flex: 0 0 50px;
    border-radius: 50%;
    position: relative;
    &:after {
      content: '';
      display: block;
      width: 10px;
      height: 10px;
      position: absolute;
      bottom: 2px;
      right: 2px;
      background: #D6D6D6;
      border: 1.5px solid #FFFFFF;
      box-sizing: border-box;
      border-radius: 50%; }
    img {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      overflow: hidden; } }
  &__desc {
    flex-grow: 1;
    margin: 0 auto 0 16px;
    @include r(1499) {
      max-width: 130px; }
    @include r(1199) {
      max-width: 100%; }
    @include r(414) {
      max-width: 160px; } }
  &__box {
    flex-grow: 1;
    margin: 0 auto 0 16px;
    @include r(414) {
      max-width: 160px; } }
  &__name {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    color: #353535;
    @include fz(16, 14); }
  &__text {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    color: #767676;
    max-width: 220px;
    @include fz(14, 12);
    @include line(1); }
  &__playing {
    display: flex;
    align-items: center;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    color: #767676;
    b {
      display: inline-block;
      font-style: normal;
      font-weight: 700;
      font-size: 14px;
      line-height: 19px;
      color: #767676;
      margin-left: 5px;
      @include line(1); } }
  &__info {
    display: flex;
    flex-direction: column;
    align-items: flex-end; }
  &__time {
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    color: #767676; }
  &__count {
    width: 18px;
    height: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F46119;
    border-radius: 50%;
    font-style: normal;
    font-weight: 700;
    font-size: 10px;
    line-height: 1;
    color: #FFFFFF;
    margin-top: 6px; }

  &.--active {
    .user-item__avatar {
      &:after {
        background: $accent; } }
    .user-item__text {
      color: #353535; }
    .user-item__time {
      color: $accent; } } }


.chat-messages-box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  background: #fff;
  height: 100%;
  border-radius: 10px; }
.chat-messages-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 24px;
  border-bottom: 1px solid rgba(226, 226, 226, 1);
  @include r(767) {
    padding-right: 12px; }
  a {
    width: 24px;
    height: 24px;
    font-size: 24px;
    color: $accent;
    & + a {
      margin-left: 24px;
      @include r(767) {
        margin-left: 12px; } } } }
.chat-messages-body {
  padding: 37px 24px;
  flex-grow: 1;
  max-height: 600px;
  overflow: auto;

  scrollbar-color: $accent #E2EBEC;
  scrollbar-width: thin!important;
  -moz-appearance: none !important;

  &::-webkit-scrollbar {
    width: 4px; }

  &::-webkit-scrollbar-track {
    background-color: #E2EBEC; }

  &::-webkit-scrollbar-thumb {
    background-color: $accent; }

  @include r(767) {
    padding: 12px; } }

.chat-messages-footer {
  padding: 10px 30px;
  border-top: 1px solid rgba(226, 226, 226, 1);
  @include r(767) {
    padding: 12px; } }
.chat-messages-form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  .chat-messages-form-btns {
    display: flex;
    align-items: center;
    margin-right: 12px;
    button {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 24px;
      height: 24px;
      flex: 0 0 24px;
      font-size: 24px;
      color: #646464;
      outline: none;
      border: none;
      cursor: pointer;
      @include tr(0.25s);
      @include hh {
        color: $accent; }
      & + button {
        margin-left: 12px; } } }

  .chat-messages-form-controls {
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
    height: 45px;
    background: #F5F5F5;
    border-radius: 25px;
    padding: 0 20px;
    flex-grow: 1;
    @include r(767) {
      padding: 0 12px; }
    input {
      width: 100%;
      background: none;
      border: none;

      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 20px;
      color: #6E6E6E;

      @include placeholder() {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 20px;
        color: #6E6E6E; }

      @include hh {
        outline: none;
        border: none; } } }
  .chat-messages-form-btn {
    margin-left: 12px;
    button {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 24px;
      height: 24px;
      flex: 0 0 24px;
      font-size: 24px;
      color: #646464;
      outline: none;
      border: none;
      cursor: pointer;
      @include tr(0.25s);
      @include hh {
        color: $accent; } } } }

.messages-item {
  & + .messages-item {
    margin-top: 15px; } }

.messages-item.--your-message {
  display: flex;
  align-items: center;
  position: relative;
  .messages-item__avatar {
    width: 40px;
    height: 40px;
    flex: 0 0 40px;
    margin-right: 9px; }
  .messages-item__text {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 21px;
    color: #353535;
    padding: 8px 20px;
    background: #F5F5F5;
    border-radius: 20px;
    max-width: 240px;
    @include fz(16, 12); } }

.messages-item.--friend-message {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;
  .messages-item__text {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 21px;
    color: #fff;
    padding: 8px 20px;
    background: $accent;
    border-radius: 20px;
    max-width: 240px;
    @include fz(16, 12); } }
