.js-trending {
  .game-card__media {
    min-height: 296px;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      height: 100%; } } }

.recommend-slide {
  display: block;
  position: relative;
  background: #FFFFFF;
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.03);
  border-radius: 10px;
  padding: 10px;
  &__box {
    display: block;
    position: relative;
    background: #DD2138;
    border-radius: 10px;
    padding: 30px;
    @include r(575) {
      padding: 15px 15px 30px; }
    &::before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 60%;
      height: 100%;
      background: linear-gradient(270deg, rgba(217, 37, 59, 0.042) 0%, rgba(0, 0, 0, 0.306) 58.85%);
      border-radius: 10px;
      z-index: 0; } }
  &__info {
    max-width: 450px;
    display: block;
    position: relative;
    z-index: 2; }
  &__logo {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 10px; }
  &__intro {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 21px;
    color: #FFFFFF;
    margin-bottom: 10px; }
  &__rating-and-price {
    display: flex;
    align-items: center;
    margin-bottom: 10px; }
  &__rating {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 21px;
    color: #EFEFEF;
    margin-right: 10px;
    @include ico() {
      color: #FF9900; } }
  &__price {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    color: #FFFFFF; }
  &__btn {
    margin-bottom: 10px;
    .uk-button-danger {
      height: 40px;
      background: #F46119;
      border-radius: 15px;
      font-style: normal;
      font-weight: 700;
      font-size: 16px;
      line-height: 21px;
      color: #FFFFFF; } }
  &__platform {
    display: flex;
    align-items: center;
    font-size: 24px;
    color: #fff;
    & > * {
      margin: 0 10px 0 0;
      &:last-child {
        margin: 0; } } }
  &__img {
    display: block;
    position: absolute;
    right: 0;
    bottom: 0; }
  &__favorites {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 20px;
    right: 20px;
    width: 40px;
    height: 40px;
    flex: 0 0 40px;
    background: rgba(0, 0, 0, 0.2);
    border-radius: 50%;
    @include ico() {
      width: 24px;
      height: 24px;
      font-size: 24px;
      color: #fff; } } }

.game-card {
  display: block;
  position: relative;
  & + .game-card {
    margin-top: 12px; }
  &__box {
    display: flex;
    flex-direction: column;
    padding: 10px;
    background-color: #fff;
    box-shadow: 0 5px 15px rgba(#000, 0.08);
    border-radius: 10px; }
  &__media {
    display: block;
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    height: 200px;
    img {
      width: 100%;
      height: 100%;
      min-height: 100%;
      object-fit: cover;
      object-position: top center;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); } }
  &__info {
    padding: 6px; }
  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    color: #353535;
    @include line(1); }
  &__genre {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    color: #767676;
    margin: 5px 0; }
  &__discussion {
    display: flex;
    align-items: center;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    color: #767676;
    b {
      display: inline-block;
      margin-left: 5px;
      color: #616161; } }
  &__rating-and-price {
    display: flex;
    align-items: center;
    margin-bottom: 10px; }
  &__rating {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 21px;
    color: #616161;
    margin-right: 10px;
    @include ico() {
      color: #FF9900;
      margin-left: 10px; } }
  &__price {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    color: #616161; }
  &__bottom {
    display: flex;
    justify-content: space-between;
    margin-top: 10px; }
  &__platform {
    display: flex;
    align-items: center;
    font-size: 24px;
    color: #616161;
    & > * {
      margin: 0 10px 0 0;
      &:last-child {
        margin: 0; } } }
  &__users {
    display: block;
    position: relative; }
  &__more {
    margin-left: auto;
    margin-right: 40px;
    @include r(575) {
      margin: 0;
      position: absolute;
      bottom: 20px;
      right: 20px; }
    a {
      font-size: 6px;
      color: #646464;
      @include tr(0.25s);
      @include h {
        color: $accent; } } }

  &.--horizontal {
    .game-card__box {
      flex-direction: row;
      align-items: center;
      @include r(575) {
        flex-direction: column; } }
    .game-card__media {
      width: 50%;
      max-width: 180px;
      max-height: 295px;
      @include r(575) {
        width: 100%;
        max-width: 100%;
        min-height: 200px; } }
    .game-card__info {
      width: 50%;
      padding: 10px 20px;
      @include r(575) {
        width: 100%;
        padding: 10px 0; } } }

  &.--small {
    .game-card__media {
      width: 50%;
      max-width: 150px;
      max-height: 90px;
      @include r(575) {
        width: 100%;
        max-width: 100%;
        max-height: none;
        min-height: 90px; } } } }

.users-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  li {
    margin-left: -10px;
    &:nth-child(1) {
      z-index: 4; }
    &:nth-child(2) {
      z-index: 3; }
    &:nth-child(3) {
      z-index: 2; }
    &:nth-child(4) {
      z-index: 1; } } }
