.page-first-screen {
  display: flex;
  width: 100%;
  min-height: 100vh;
  background-image: url(../img/bg-first-screen.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 120px 160px;
  padding: 11.1vh 8.33vw;

  @include r(959) {
    padding: 50px 15px 50px 30px; }

  @include r(767) {
    text-align: center; }


  h1 {
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 32px;
    color: #FFFFFF;
    margin: 5px 0 0 0;
    @include fz(24, 18); } }


.form-login {
  display: block;
  position: relative;
  margin-left: auto;
  width: 100%;
  max-width: 540px;
  background: #FFFFFF;
  box-shadow: 0px 30px 70px rgba(0, 0, 0, 0.03);
  border-radius: 25px;
  padding: 60px 45px;
  box-sizing: border-box;
  @include r(959) {
    padding: 30px 20px; }
  @include r(767) {
    margin: auto; }
  &__social {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 25px; }
  &__box {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 21px;
    color: #6C6C6C;

    .uk-heading-line {
      margin-bottom: 35px;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 21px;
      color: #6C6C6C; }

    a:not(.uk-button) {
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 21px;
      color: #F46119; }

    .uk-button {
      height: 60px; } } }


